import React, { Component} from 'react';
import {Biglink} from "./Biglink";

export class Biglinks extends Component {

    getLinks() {
        return this.props.array.map(element =>
            <Biglink
                type={element.type}
                path={element.link}
                text={element.name}/>
        );
    }

    render() {
        return (
            <div className={"biglinks"}>{this.getLinks()}</div>
        )
    }
}