import React, { Component} from 'react';
import nextId from "react-id-generator";
import Button from "../main/presentation/Button";
import strings from "../Localisation";
import {sendAmplitudeData} from "../../utils/Amplitude";

export default class Form extends Component {

    constructor(props) {
        super(props);
        this.buttonId = nextId();
    }

    componentDidMount() {
        let button = document.getElementById(this.buttonId);
        let link = this;

        button.addEventListener('click', function (event) {
            event.preventDefault();

            sendAmplitudeData("Merch: opening form on " + link.props.title);

            const openWhere = (link.props.type !== "ordinary")? "_blank" : "_self";
            window.open(link.props.path, openWhere);
        });
    }

    render() {
        return (
            <div className={"merch_right"}>
                <div className={"merch_title"}>{this.props.title}</div>
                <div className={"merch_description"}>{this.props.description}</div>
                <div className={"merch_price"}>{this.props.price}</div>

                <Button id={this.buttonId} text={strings.merch__presell} />
            </div>
        )
    }
}