import React, {Component} from 'react';
import Dot from './Dot';
import Phone from './Phone';
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../../utils/Amplitude";

export default class PhoneContainer extends Component {
    constructor(props) {
        super(props);
        let link = this;

        this.dots_ids = [];
        this.phones_ids = [];
        this.dotsMenuId = nextId();
        this.phoneContainerId = nextId();
        this.phoneContainedPhonesId = nextId();

        for (let element of this.props.array) {
            let id = nextId();

            this.phones_ids[this.phones_ids.length] = "phone_" + id;
            this.dots_ids[this.dots_ids.length] = id;
        }

        console.log(this.dots_ids);
        this.state = {
            active_id: link.dots_ids[0],
            active_phone_id: link.phones_ids[0]
        };
    }

    getListOfDots() {
        return this.dots_ids.map(element =>
            <Dot
                id={element}
                isActive={element === this.state.active_id }/>
        );
    }

    getListOfPhones() {
        return this.phones_ids.map(element =>
            <Phone
                index={this.phones_ids.length - (this.phones_ids.indexOf(element) + 1)}
                maxIndex={this.phones_ids.length - 1}
                id={element}
                src={this.props.array[this.phones_ids.length - (this.phones_ids.indexOf(element) + 1)]}
                isActive={element === this.state.active_phone_id} />
        );
    }

    /*

    resizePhoneContainer(containers) {
        if (document.body.clientWidth < 500) {
            var rect = containers.getBoundingClientRect();
            console.log(rect.top, rect.right, rect.bottom, rect.left);

            containers.style.right =  35 * (500 - document.body.clientWidth) / 45 + "px";
        }
    }

     */

    resizePhoneContainer(containers) {
        let clientWidth = document.body.clientWidth

        if (clientWidth < 500 && clientWidth > 200) {
            let rect = containers.getBoundingClientRect();

            let trueRight = clientWidth - rect.right - 30;
            let difference = trueRight - rect.left;

            let leftBasicMargin = (clientWidth <= 420)? 40 : 64;

            containers.style.left = 6 + rect.left + difference / 2 -  leftBasicMargin + "px";
        }
    }

    resetContainerWidth() {
        let phoneContainers = document.getElementById(this.phoneContainedPhonesId);
        let containers = document.getElementById(this.phoneContainerId);
        let dotsMenu = document.getElementById(this.dotsMenuId);
        let link = this;

        let width = (this.phones_ids.length - 1) * 57 + 297;

        phoneContainers.style.width = width + "px";
        containers.style.width = width + "px";
        dotsMenu.style.width = (this.phones_ids.length * 25) + "px";

        window.addEventListener("resize", function(event) {
            link.resizePhoneContainer(containers);
        });

        this.resizePhoneContainer(containers);
    }

    componentDidMount() {
        this.resetContainerWidth();
        let link = this;

        if (this.phones_ids.length > 1) {
            for (let id of this.dots_ids) {
                let element = document.getElementById(id);
                element.addEventListener('click', function(e) {
                    e.preventDefault();

                    sendAmplitudeData("PhoneContainer: clicked on another phone by dot");

                    link.setState({
                        active_id: element.id,
                        active_phone_id: "phone_" + element.id
                    })
                })
            }

            for (let id of this.phones_ids) {
                let element = document.getElementById(id);
                element.addEventListener('click', function(e) {
                    e.preventDefault();

                    sendAmplitudeData("PhoneContainer: clicked on another phone by phone");

                    link.setState({
                        active_phone_id: element.id,
                        active_id: element.id.replace("phone_", "")
                    })
                })
            }
        }
    }

    render() {
        return (
            <div className={"functional__phone_container"} id={this.phoneContainerId}>
                <div className={"functional__phone_container__phones"} id={this.phoneContainedPhonesId}>
                    {this.getListOfPhones()}
                </div>

                {
                    <div className={"functional__dots_menu"} id={this.dotsMenuId}>
                        {(this.phones_ids.length > 1)? this.getListOfDots() : []}
                    </div>
                }

            </div>
        )
    }
}