import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import {Biglinks} from "./Biglinks";

import {PATH_CHARACTERISTICS, PATH_IDEA, PATH_INSTRUCTIONS, PATH_ROADMAP, PATH_TEAM} from "../../utils/Paths";
import strings from "../Localisation";

export class About extends Component {

    getLinks() {
        return [
            {
                name: strings.footer__about_team,
                link: PATH_TEAM,
                type: "ordinary"
            },
            {
                name: strings.footer__about_instructions,
                link: PATH_INSTRUCTIONS,
                type: "ordinary"
            },
            {
                name: strings.footer__about_characteristics,
                link: PATH_CHARACTERISTICS,
                type: "ordinary"
            },
            {
                name: strings.footer__about_next,
                link: PATH_ROADMAP,
                type: "ordinary"
            },
            {
                name: strings.footer__about_privacy,
                link: strings.privacy__link,
                type: "ordinary"
            }
        ]
    }

    constructor(props) {
        super(props);
        this.state = {};
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"about_up_space"} />
                <Biglinks array={this.getLinks()} />
                <div className={"about_down_space"} />
                <Footer />
            </div>
        )
    }
}