import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import strings from "../Localisation";
import Shirt from "./Shirt";
import Form from "./Form";

export default class Merch extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"merch_up_space"} />

                <div className={"merch_container"}>
                    <div className={"merch_shirt__container"}>
                        <Shirt
                            src={"/images/merch/tshirt/black.png"} />

                        <Shirt
                            src={"/images/merch/tshirt/white.png"} />
                    </div>

                    <Form
                        title={strings.merch__tshit_title}
                        description={strings.merch__tshit_description}
                        price={strings.merch__tshit_price}/>
                </div>

                <div className={"merch_middle_space"} />

                <div className={"merch_container"}>
                    <div className={"merch_shirt__container"}>
                        <Shirt
                            src={"/images/merch/hoodie/black.png"} />

                        <Shirt
                            src={"/images/merch/hoodie/white.png"} />
                    </div>

                    <Form
                        title={strings.merch__hoodie_title}
                        description={strings.merch__hoodie_description}
                        price={strings.merch__hoodie_price}/>
                </div>

                <div className={"merch_down_space"} />
                <Footer />
            </div>
        )
    }
}