import React, {Component} from "react";
import Functional from "../main/functional/Functional";
import strings from "../Localisation";
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";

export class Instructions extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    getControllingPhoneArray() {
        return [
            "/images/characteristics/characteristics_1.gif",
            "/images/characteristics/characteristics_2.gif",
            "/images/characteristics/characteristics_3.gif"
        ]
    }

    getControllingComputerArray() {
        return [
            "/images/instructions/computer/computer_2.gif",
            "/images/instructions/computer/computer_1.gif"
        ]
    }

    getEyesHealthArray() {
        return [
            "/images/main/health/health_2.gif",
            "/images/main/health/health_1.gif"
        ]
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"instructions_up_space"} />

                <Functional
                    array={this.getControllingPhoneArray()}
                    title={strings.instructions__controlling_phone__title}
                    isDown={true}
                    description={strings.instructions__controlling_phone__description} />

                <div className={"instructions_space_1"} />

                <Functional
                    array={this.getControllingComputerArray()}
                    title={strings.instructions__controlling_computer__title}
                    isDown={true}
                    description={strings.instructions__controlling_computer__description} />

                <div className={"instructions_space_2"} />

                <Functional
                    array={this.getEyesHealthArray()}
                    title={strings.instructions__keeping_eyes_health__title}
                    isDown={true}
                    description={strings.instructions__keeping_eyes_health__description} />

                <div className={"instructions_down_space"} />
                <Footer />
            </div>
        )
    }
}