import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import strings from "../Localisation";
import Functional from "../main/functional/Functional";

export class Characteristics extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    getHealthArray() {
        return [
            "/images/characteristics/characteristics_1.gif",
            "/images/characteristics/characteristics_2.gif",
            "/images/characteristics/characteristics_3.gif",
            "/images/characteristics/characteristics_4.gif"
        ]
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"characteristics_up_space"} />

                <Functional
                    array={this.getHealthArray()}
                    title={strings.characteristics__title}
                    isDown={true}
                    description={strings.characteristics__description} />

                <div className={"characteristics_down_space"} />
                <Footer />
            </div>
        )
    }
}