import React, { Component} from 'react';
import Functional from '../functional/Functional'
import strings from "../../Localisation";

export default class Health extends Component {
    getHealthArray() {
        return [
            "/images/main/health/health_2.gif",
            "/images/main/health/health_1.gif"
        ]
    }

    render() {
        return (
           <div>
               <div className={"health_up_space"}/>
               <Functional
                   path={"https://www.youtube.com/watch?v=0szQHEQ7MXA"}
                   array={this.getHealthArray()}
                   title={strings.health__title}
                   description={strings.health__description}
                   emoji={"/images/main/health/eyes.png"} />
           </div>
        )
    }
}