import React, { Component } from 'react';

export class Column extends Component {
    render() {
        return (
            <div className={"footer_column"} id={this.props.id}>
                <div className="footer_column__title">
                    {this.props.title}
                </div>
                <div className="footer_column__content">
                    {this.props.children}
                </div>
            </div>
        )
    }
}
