import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import {Biglinks} from "./Biglinks";
import strings from "../Localisation";

export class Contact extends Component {

    getLinks() {
        return [
            {
                name: strings.footer__contact_mail,
                link: "mailto:reface@gmail.com",
                type: "ordinary"
            },
            {
                name: strings.footer__contact_instagram,
                link: "https://instagram.com/reface_tech",
                type: "instagram"
            },
            {
                name: strings.footer__contact_telegram,
                link: "https://t.me/reface_tech",
                type: "telegram"
            },
            {
                name: strings.footer__contact_facebook,
                link: "https://facebook.com/refacetech",
                type: "facebook"
            },
            {
                name: strings.footer__contact_vkontakte,
                link: "https://vk.com/reface_tech",
                type: "vkontakte"
            },
            {
                name: strings.footer__contact_patreon,
                link: "https://patreon.com/reface_tech",
                type: "patreon"
            }
        ]
    }

    constructor(props) {
        super(props);
        this.state = {};
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"contact_up_space"} />
                <Biglinks array={this.getLinks()} />
                <div className={"contact_down_space"} />
                <Footer />
            </div>
        )
    }
}