import React, { Component} from 'react';
import strings from "../Localisation";
import Doings from "./Doings";
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import Card from "./Card";

export default class Roadmap extends Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    handleStateUpdate() {
        this.setState({});
    }

    getDoneCards() {
        return [
            <Card
                date={strings.doings__done__app__date}
                description={strings.doings__done__app__description}
                src={ "/images/roadmap/Neural net.svg"}
                title={strings.doings__done__app__title}/>,

            <Card
                date={strings.doings__done__server__date}
                description={strings.doings__done__server__description}
                src={ "/images/roadmap/Database.svg"}
                title={strings.doings__done__server__title} />,

            <Card
                date={strings.doings__done__checker__date}
                description={strings.doings__done__checker__description}
                src={ "/images/roadmap/Check.svg"}
                title={strings.doings__done__checker__title} />,

            <Card
                date={strings.doings__done__retrain__date}
                description={strings.doings__done__retrain__description}
                src={ "/images/roadmap/Refresh.svg"}
                title={strings.doings__done__retrain__title} />,

            <Card
                date={strings.doings__done__computer__date}
                description={strings.doings__done__computer__description}
                src={ "/images/roadmap/Computer.svg"}
                title={strings.doings__done__computer__title} />,

            <Card
                date={strings.doings__done__automatic__date}
                description={strings.doings__done__automatic__description}
                src={ "/images/roadmap/Camera.svg"}
                title={strings.doings__done__automatic__title}/>,

            <Card
                date={strings.doings__done__scheme__date}
                description={strings.doings__done__scheme__description}
                src={ "/images/roadmap/Scheme.svg"}
                title={strings.doings__done__scheme__title}/>
        ]
    }

    getWillBeDoneCards() {
        return [
            <Card
                date={strings.doings__not_done__prototype__date}
                description={strings.doings__not_done__prototype__description}
                src={ "/images/roadmap/Coin.svg"}
                title={strings.doings__not_done__prototype__title}/>,

            <Card
                description={strings.doings__not_done__crowdfounding__description}
                src={ "/images/roadmap/3D.svg"}
                title={strings.doings__not_done__crowdfounding__title}
                date={strings.doings__not_done__crowdfounding__date}/>,

            <Card
                date={strings.doings__not_done__apple__date}
                description={strings.doings__not_done__apple__description}
                src={ "/images/roadmap/Apple.svg"}
                title={strings.doings__not_done__apple__title}/>
        ]
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"roadmap_up_space"}/>

                <div className={"roadmap_title"}>{strings.done__title}</div>
                <Doings array={this.getDoneCards()} />

                <div className={"roadmap_middle_space"} />

                <div className={"roadmap_title"}>{strings.will_do__title}</div>
                <Doings array={this.getWillBeDoneCards()} />

                <div className={"roadmap_down_space"}/>
                <Footer />
            </div>
        )
    }
}