import React, { Component} from 'react';
import Functional from '../functional/Functional'
import strings from "../../Localisation";

export default class Health extends Component {
    getControlArray() {
        return [
            "/images/main/control/control_1.gif",
            "/images/main/control/control_2.gif",
            "/images/main/control/control_3.gif"
        ]
    }

    render() {
        return (
           <div>
               <div className={"control_up_space"}/>
               <Functional
                   path={"https://www.youtube.com/watch?v=KcsvcFPbR_4"}
                   array={this.getControlArray()}
                   title={strings.control__title}
                   isDown={true}
                   description={strings.control__description}
                   emoji={"/images/main/control/button.png"} />
           </div>
        )
    }
}