import React, { Component} from 'react';
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../utils/Amplitude";
let classNames = require('classnames');

export class Biglink extends Component {

    constructor(props) {
        super(props);

        this.buttonId = nextId();
    }

    componentDidMount() {
        let element = document.getElementById(this.buttonId);
        let link = this;

        element.addEventListener('click', function (event) {
            event.preventDefault();

            sendAmplitudeData("Biglink: opening " + link.props.path);

            const openWhere = (link.props.path[0] === "/")? "_self" : "_blank";
            window.open(link.props.path, openWhere);
        })
    }

    render() {
        let className = classNames('biglink', {
            'link__ordinary': this.props.type === "ordinary",
            'link__instagram': this.props.type === 'instagram',
            'link__telegram': this.props.type === 'telegram',
            'link__facebook': this.props.type === 'facebook',
            'link__vkontakte': this.props.type === 'vkontakte',
            'link__patreon': this.props.type === 'patreon'
        });

        return (
            <div className={className} id={this.buttonId}>{this.props.text}</div>
        )
    }
}