import React, { Component} from 'react';
import strings from "../Localisation";
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../utils/Amplitude";

export class Contact extends Component {
    constructor(props) {
        super(props);

        this.buttonId = nextId();
    }

    componentDidMount() {
        let button = document.getElementById(this.buttonId);
        let link = this;

        button.addEventListener('click', function (event) {
            event.preventDefault();

            sendAmplitudeData("Contact: writing to " + link.props.type);
            window.open(link.props.path, "_blank");
        })
    }

    render() {
        return (
            <div className={"team_contact"}>
                <div className={"team_contact__header"}>
                    <img className={"team_contact__image"} src={this.props.src} />
                    <div className={"team_contact__title"}>{this.props.title}</div>
                </div>

                <div className={"team_contact__description"}>{this.props.description}</div>

                <div className={"team_contact__button"} id={this.buttonId}>{strings.team__contact_button}</div>
            </div>
        )
    }
}