import React, { Component} from 'react';
let classNames = require('classnames');

export default class LanguageItem extends Component {
    render() {
        let className = classNames('header__language_button', {
            'header__language_button__active': this.props.isActive === true,
            'header__language_button__inactive': this.props.isActive === false
        });

        return (
            <p className={className} id={this.props.id}>{this.props.text}</p>
        )
    }
}