import React, {Component} from "react";
import nextId from "react-id-generator";
import axios from 'axios';
import cookie from 'react-cookies'
import strings from "../../Localisation";
import version from "../../../config"
import {sendAmplitudeData} from "../../../utils/Amplitude";
let classNames = require('classnames');

export default class ShortForm extends Component {
    constructor(props) {
        super(props);

        this.buttonId = nextId();
        this.inputId = nextId();

        let email = cookie.load("email");

        this.state = {
            user_email__text: email? email : "",
            is_error: undefined,
            toast: undefined
        }
    }

    getMailRegex() {
        return new RegExp("(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|\"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\\\[\x01-\x09\x0b\x0c\x0e-\x7f])*\")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\\])")
    }

    sendRequest() {
        sendAmplitudeData("Footer: sending form");

        const language = strings.getLanguage();
        const mail = document.getElementById(this.inputId).value.replace("\ \g", "");
        const link = this;

        if (mail.length > 0 && language.length > 0) {
            if (this.getMailRegex().test(mail)) {
                axios.get("https://maker.ifttt.com/trigger/email_sent/with/key/dZ2N1BVOi6c5zCIOD27V9Q?value1=" + mail  + "&value2=" + language + "&value3=" + version.version)
                    .then(function (response) {
                        link.setSuccess("  ", mail)

                    })
                    .catch(function (error) {
                        console.log(error.message);

                        if (error.message.includes("Network Error")) {
                            link.setSuccess("  ", mail);
                        }

                        else {
                            link.setError("  ", mail);
                        }
                    });
            }

            else {
                this.setError("  ")
            }
        }

        else {
            this.setError("  ")
        }
    }

    setError(message, mail) {
        cookie.save("email",  mail);
        this.setState(
            {
                is_error: true,
                toast: message,
                user_email__text: mail
            }
        )
    }

    setSuccess(message, mail) {
        cookie.save("email",  mail);
        this.setState(
            {
                is_error: false,
                toast: message,
                user_email__text: mail
            }
        )
    }

    componentDidMount() {
        let button = document.getElementById(this.buttonId);
        let input = document.getElementById(this.inputId);
        const link = this;

        input.addEventListener('submit', function(e) {
            console.log(e);
            e.preventDefault();
            button.click();
        });

        input.addEventListener('keypress', function (e) {
            let key = e.key || e.altKey;
            console.log(key);

            if (key === 'Enter') {
                e.preventDefault();
                button.click();
            }

            else {

            }
        });

        button.addEventListener("click", function(event) {
            event.preventDefault();
            link.sendRequest();
        });
    }

    render() {
        let classNameToast = classNames("footer__short_form__toast", {
            "footer__short_form__toast__error": this.state.is_error === true,
            "footer__short_form__toast__success": this.state.is_error === false
        });

        return (
            <div>
                <div className={"footer__short_form"}>
                    <div className={"footer__short_form__content"}>
                        <input type="text" size="20" placeholder={"your@email.com"} id={this.inputId} className={"footer__input"}/>
                        <div id={this.buttonId} className={"footer__button"}>{strings.footer__button}</div>
                    </div>
                </div>
                <div className={classNameToast}>{this.state.toast}</div>
            </div>
        )
    }
}