import React, { Component} from 'react';
let classNames = require('classnames');

export default class Title extends Component {
    render() {
        let className = classNames('functional__title_emoji', {
            'functional__title_emoji__down': this.props.isDown === true
        });

        return (
            <div className={"functional__title"}>
                <div className={"functional__title_text"}>{this.props.title}</div>
                <img className={className} src={this.props.src}/>
            </div>
        )
    }
}