import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import {Biglinks} from "./Biglinks";

import {PATH_MAIN, PATH_ABOUT, PATH_CHARACTERISTICS,
    PATH_CONTACT, PATH_IDEA, PATH_INSTRUCTIONS,
    PATH_MERCH, PATH_ROADMAP, PATH_STUFF, PATH_TEAM} from "../../utils/Paths";
import strings from "../Localisation";

export class Stuff extends Component {

    getLinks() {
        return [
            {
                name: strings.footer__stuff_lib,
                link: "https://www.notion.so/mixedproject/Android-Library-41c90cc70c2f48a5875d7ac1bda879b1",
                type: "ordinary"
            },
            {
                name: strings.footer__stuff_kit,
                link: strings.footer__stuff_kit_link,
                type: "ordinary"
            },
            {
                name: strings.footer__stuff_merch,
                link: PATH_MERCH,
                type: "ordinary"
            }
        ]
    }

    constructor(props) {
        super(props);
        this.state = {};
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"stuff_up_space"} />
                <Biglinks array={this.getLinks()} />
                <div className={"stuff_down_space"} />
                <Footer />
            </div>
        )
    }
}