import React, { Component} from 'react';
let classNames = require('classnames');

export default class Dot extends Component {
    render() {
        let className = classNames('functional__phone_container__menu_element', {
            'functional__phone_container__menu_active_element': this.props.isActive === true,
            'functional__phone_container__menu_passive_element': this.props.isActive !== true,
        });

        return (
            <div className={className} id={this.props.id}/>
        )
    }
}