import React, { Component} from 'react';
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../../utils/Amplitude";

export class Logo extends Component {
    constructor(props) {
        super(props);

        this.buttonId = nextId();
    }

    componentDidMount() {
        let element = document.getElementById(this.buttonId);

        element.addEventListener('click', function (event) {
            event.preventDefault();

            sendAmplitudeData("Footer: clicked on logo");

            window.open("/", "_self");
        })
    }

    render() {
        return (
            <div id={this.buttonId} className={"footer_logo"}>Reface</div>
        )
    }
}