import React, { Component} from 'react';
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../../utils/Amplitude";
let classNames = require('classnames');

export default class MenuItem extends Component {
    constructor(props) {
        super(props);
        this.buttonId = nextId();
    }

    componentDidMount() {
        let link = this;
        let button = document.getElementById(this.buttonId);

        button.addEventListener('click', function (event) {
            event.preventDefault();

            sendAmplitudeData("Header: opening " + link.props.path);

            const openWhere = (link.props.type !== "ordinary")? "_blank" : "_self";
            window.open(link.props.path, openWhere);
        })
    }

    render() {
        let className = classNames('menu_item', {
            'menu_item__ordinary': this.props.type === "ordinary",
            'menu_item__survey': this.props.type === 'survey',
            'menu_item__main': this.props.type === 'main'
        });

        return (
            <div className={"menu_item__container"}>
                <p className={className} id={this.buttonId}>{this.props.text}</p>
                {
                    (this.props.type === 'survey')? <img className={"menu_item__survey_icon"} src={"/images/main/header/arrow.svg"} /> : <div></div>
                }
            </div>
        )
    }
}