import React, { Component} from 'react';
import {Team} from "./Team";
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";

export class Screen extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <div className={"team_up_space"} />
                <Team />
                <div className={"team_down_space"} />
                <Footer />
            </div>
        )
    }
}