import React, { Component} from 'react';
import nextId from "react-id-generator";
import strings from "../../Localisation";
import {sendAmplitudeData} from "../../../utils/Amplitude";

export default class HowItWorks extends Component {
    constructor(props) {
        super(props);
        this.buttonId = nextId();
    }

    componentDidMount() {
        let link = this;
        let button = document.getElementById(this.buttonId);

        button.addEventListener('click', function (event) {
            event.preventDefault();

            sendAmplitudeData("HowItWorks: opened youtube video");
            window.open(link.props.path, '_blank');
        })
    }

    render() {
        return (
            <div className={"functional__how_it_works"} id={this.buttonId}>
                <div className={"functional__how_it_works__title"}>{strings.functional__how_it_works}</div>
                <img className={"functional__how_it_works__image"} src={"/images/main/functional/play.svg"}/>
            </div>
        )
    }
}