import React, { Component} from 'react';

export default class Card extends Component {

    render() {
        return (
            <div className={"doing_card"}>
                <div className={"doing_card__header"}>
                    <img className={"doing_card__image"} src={this.props.src}/>
                    <div className={"doing_card__title"}>{this.props.title}</div>
                </div>

                <div className={"doing_card__description"}>{this.props.description}</div>

                <div className={"doing_card__date"}>{this.props.date}</div>
            </div>
        )
    }
}